body {
    background-color: blue;
    margin: 0;
    padding: 0;
}

.new-note{
    background-color: white;
    padding: 10px 12px;
    border-radius: 3px;

    &__input {
        width: 100%;
        border: none;

        &:focus {
            outline: none;
        }
    }

}

.container{
    margin: 0 auto;
    padding: 20px;
    max-width: 600px;
}


.note-list{
    margin: 40px 0;

}

.note {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    background-color: white;
    padding: 10px 12px;
    border-radius: 3px;

    &__text{
        flex: 1;
    }
    &__input {
        flex: 1;
        border-top: 0;
        border-right: 0;
        border-left: 0;
        border-bottom: 1px solid #c7c7c7;



    }
    &__button{
        background-color: transparent;
        border: 0;
        cursor: pointer;

        &--hidden{
            visibility: hidden;
        }

        &--red {
            color: #f44336
        }
        &--green{
            color: #4caf50
        }

    }
}

